.about__btn {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100px;
  height: 100px;
  transform: translate(-50%, -50%);
  border: none;
  background-color: transparent;
  z-index: 3;
  padding: 0;
  border-radius: 50%;
  cursor: pointer;
  fill: #ffffff;
}
.about__btn::before, .about__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.about__btn::before {
  width: 70px;
  height: 70px;
  padding: 2px;
  background-image: linear-gradient(81deg, #003d85 0%, #b80027 89%);
}
.about__btn::after {
  width: 100px;
  height: 100px;
  padding: 2px;
  background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(237, 2, 53, 0.2)), to(rgba(0, 61, 183, 0.2)));
  background-image: linear-gradient(to left top, rgba(237, 2, 53, 0.2), rgba(0, 61, 183, 0.2));
  -webkit-mask: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff)) content-box, -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff)) content-box, -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff));
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
.about__btn:hover, .about__btn:focus {
  fill: #102654;
}
.about__btn:hover::before, .about__btn:focus::before {
  background-color: #ffffff;
  background-image: none;
}
.about__btn:hover::after, .about__btn:focus::after {
  background-color: #102654;
  background-image: none;
}
.about__btn:active {
  fill: #102654;
}
.about__btn:active::before {
  width: 58px;
  height: 58px;
  background-color: #ffffff;
  background-image: none;
}
.about__btn:active::after {
  background-color: #102654;
  background-image: none;
}
.about__btn:disabled {
  opacity: 0.4;
  fill: #102654;
}
.about__btn:disabled::before {
  background-color: #ffffff;
  background-image: none;
}
.about__btn:disabled::after {
  background-color: #102654;
  background-image: none;
}

